import { ThreeDots } from "react-loader-spinner";
import "../style/loader.css";

const Loader = () => {
  return (
    <div className="loader">
      Legran
      <ThreeDots
        height="70"
        width="70"
        radius="7"
        color="orange"
        ariaLabel="three-dots-loading"
      />
    </div>
  );
};

export default Loader;
